/* Критичні ширини: 1419 905 646 - 335 */

.Main
  height: 100vh
  align-content: center
  display: grid

  .content

    h1
      animation: text 2s

    .left
      flex-direction: row-reverse
      justify-content: space-between
      display: flex

    .about-me
      margin-top: -1rem
      margin-left: 6.1875rem
      display: flex

      .line
        width: 2.5px
        height: 0
        background: #010001
        animation: line-text 2s forwards
        animation-delay: 2s
      
      .text-conteiner
        margin: 0.5rem 0 0 0.9375rem
        overflow: hidden

        .text
          margin-bottom: 1rem
          font-family: Lexend
          align-items: baseline
          display: flex
          transform: translateX(-100%)
          animation: text 3s forwards
          animation-delay: 3s
        
          h2
            letter-spacing: -0.105rem
            font-family: Lexend
            color: #0059AB
          
          h3, h4
            font-family: Lexend

          .orange
            letter-spacing: -0.075rem
            color: #DC6F2E

        .small-text
          margin-top: 2.81rem
          transform: translateX(-100%)
          animation: text 3s forwards
          animation-delay: 3s

          p
            margin-bottom: 0.94rem
            font-family: Lexend

    .me
      width: fit-content
      height: 17rem
      overflow: hidden
      img
        width: 18.75rem
        transform: translateY(100%)
        pointer-events: none
        animation: mePhoto 2s forwards
        animation-delay: 2s
      
      div
        height: 17rem
        pointer-events: none
        transform: translateY(-100%)
        background: linear-gradient(180deg, rgba(240, 248, 255, 0.07) 60%, rgba(240, 248, 255, 0.71) 80%, rgba(240, 248, 255, 0.94) 95%, #F0F8FF 99%)

@keyframes text
  from
    transform: translateX(-100%)
  to
    transform: translateX(0%)

@keyframes line-text
  from
    height: 0rem
  to
    height: 25rem

@keyframes mePhoto
  from
    transform: translateY(100%)
  to
    transform: translateY(0%)

@media (width < 1419px)
  .Main
    .content
      .about-me
        margin-left: 3vw

        .line
          background: transparent

        .text-conteiner
          .text
            margin-bottom: 0.4rem
            font-size: 1.8rem

            h2
              font-size: 2.8rem
            
            .orange
              font-size: 1.8rem

          .small-text
            margin-top: 1.4rem
      
      .me
        height: 12.8rem

        img
          width: 14rem
        
        div
          height: 14rem

@media (width < 905px)
  .Main
    height: fit-content
    margin-top: 1rem

    .content
      h1
        font-size: 5rem

      .left
        height: 35rem
        display: block
      
      .me
        height: 16.4rem
        margin: 20px auto

        img
          width: 18rem
      
      .about-me
        margin-left: 0

@media (width < 646px)
  .Main
    .content
      h1
        font-size: 3rem

      .about-me
        .text-conteiner
          .text
            font-size: 1rem

            h2
              font-size: 1.5rem

            .orange
              font-size: 1.3rem

          .small-text
            font-size: 0.8rem